.profile {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.description {
    display: flex;
    align-items: center;
    justify-content: baseline;
    flex-direction: column;
    margin-bottom: 10px;
}
.avatar {
    display: block;
    width: 100px;
    height: 100px;
    margin-bottom: 10px;
    margin-top: 20px;
}
  
.name {
    font-weight: 800;
    font-size: 24px;
    margin-bottom: 5px;
    margin-top: 10px;
}

.tag {
    font-weight: 500;
    font-size: 20px;
    color: rgb(113, 118, 118);
    margin: 0;
}
  
.location {
    font-size: 18px;
    color: rgb(113, 118, 118);
    margin: 0;
}
  
.stats {
    list-style: none;
    display: flex;
}
.stats_item {
    padding: 5px;
    width: 100px;
    height: 50px;
    display: flex;
    flex-direction: column;
    background-color: rgb(100, 149, 237);
    
    font-weight: 500;
    align-items: center;
    border-radius: 25px;
    box-shadow: 9px 5px 5px rgb(0 0 255 / 10%);
    transition: all 0.3s ease;
}
  
.stats_item:not(:last-child) {
    margin-right: 10px;
}
.stats_item:hover,
.stats_item:focus {
    transform: scale(1.1);
}